import {
  useDepartmentTabControllerMutation,
  useDepartmentTabManageQuery,
  useDepartmentCreatedHolidayMutation,
  useDepartmentAllHolidayQuery,
  useDepartmentDeleteHolidayMutation,
} from "../../../Services/DepartmentManager/departmentManagerApiDynamic";

export const useDepartmentTabManage = (args) => {
  const { data, refetch, isFetching } = useDepartmentTabManageQuery(args.data, {
    skip: args.skip,
  });
  return {
    departmentTabManage: data,
    departmentTabManageRefetch: refetch,
    departmentTabManageLoading: isFetching,
  };
};

export const useDepartmentTabController = () => {
  const [departmentTabController] = useDepartmentTabControllerMutation();
  return [departmentTabController];
};

export const useDepartmentCreatedHoliday = () => {
  const [departmentCreatedHoliday] = useDepartmentCreatedHolidayMutation();
  return [departmentCreatedHoliday];
};
export const useDepartmentAllHoliday = (args) => {
  const { data, refetch, isFetching } = useDepartmentAllHolidayQuery(
    args.data,
    {
      skip: args.skip,
    }
  );
  return {
    departmentAllHoliday: data,
    departmentAllHolidayRefetch: refetch,
    departmentAllHolidayLoading: isFetching,
  };
};

export const useDepartmentDeleteHoliday = () => {
  const [departmentDeleteHoliday] = useDepartmentDeleteHolidayMutation();
  return [departmentDeleteHoliday];
};
